.order {
    &-block {
        max-width: 682px;
    }

    &__title {
        font-weight: bold;
        font-size: 24px;
        line-height: 33px;
        color: #605DEC;
        margin-bottom: 16px;
    }

    &__subtitle {
        font-weight: normal;
        font-size: 18px;
        line-height: 25px;
        margin-bottom: 36px;
    }

    &__label {
        width: 100%;
        margin-bottom: 24px;
        &-title {
            font-weight: 600;
            font-size: 18px;
            line-height: 25px;
            color: #6E7491;
            margin-bottom: 24px;
        }

        &-block {
            display: flex;
            flex-wrap: wrap;
        }
    }

    &__checkbox {
        &-block {
            display: flex;
            align-items: center;
            margin-bottom: 24px;
        }
        &-item {
            width: 16px;
            height: 16px;
            background: #000;
            -webkit-appearance: auto;
            -moz-appearance: auto;
            appearance: auto;
        }
        &-text {
            font-weight: normal;
            font-size: 16px;
            line-height: 22px;
            color: #6E7491;
            margin-left: 8px;
        }
    }

    &__top {
        margin-bottom: 24px;
        input {
            width: 200px;
        }
    }

    &__input {
        margin-right: 24px;
        margin-bottom: 24px;
        width: 300px;
    }

    &__birth {
        display: flex;
        flex-direction: column;
        input {
            margin-bottom: 0;
            width: 252px;
        }
        &-date {
            font-weight: normal;
            font-size: 12px;
            line-height: 16px;
            color: #7C8DB0;
            margin-left: 4px;
            margin-top: 4px;
        }
    }

    &__bags {
        margin-top: 36px;
        max-width: 460px;
        &-top {
            display: flex;
            justify-content: space-between;
            margin-bottom: 15px;
        }
        &-title {
            font-weight: 600;
            font-size: 18px;
            line-height: 25px;
        }
        &-bottom {
            display: flex;
            justify-content: space-between;
        }

        &-counter {
            display: flex;
            align-items: center;
            width: 120px;
            justify-content: space-between;
        }

        &-number {
            font-weight: normal;
            font-size: 18px;
            line-height: 25px;
            color: #6E7491;
        }
    }

    &__footer {
        &-title {
            font-weight: 600;
            font-size: 18px;
            line-height: 25px;
            color: #6E7491;
            margin-bottom: 11px;
        }

        &-bottom {
            margin-top: 74px;
        }

        &-btn {
            border: 1px solid #605DEC;
            background: #fff;
            color: #605DEC;
            &:last-child {
                margin-left: 24px;
                background: rgba(203, 212, 230, 0.3);
                border: 1px solid #7C8DB0;
                color: #7C8DB0;
            }
        }
    }

}