.payment {

    &__method {
        display: flex;
        border: 1px solid #605DEC;
        border-radius: 4px;
        margin-bottom: 40px;
        justify-content: space-between;

        &-item {
            padding: 12px 20px;
            display: flex;
            align-items: center;
            &:hover {
                background: #605DEC;
                color: #FAFAFA;
                border-radius: 4px;
                span {
                    color: #FAFAFA;
                }
            }
        }

        &-text {
            color: #605DEC;

        }

        &-img {
            margin-right: 4px;
            width: 18px;
            height: 18px;
        }
    }

    &__form {
        max-width: 480px;
    }

    &__input {
        width: 100%;
    }

    &__bottom {
        display: flex;
        flex-wrap: wrap;
        &-birth {
            margin-right: 24px;
            input {
                width: 240px;
            }
        }
        &-input {
           width: 216px;
           height: 48px;
        }
    }

    &__option {
        margin-top: 36px;
        margin-bottom: 22px;
    }

    &__continue {
        margin-bottom: 48px;
    }
}