.sidebar {
    &__table {
        width: 400px;
        padding: 16px;
        border: 1px solid #E9E8FC;
        border-radius: 12px;

        &-string {
            padding: 8px;
            display: flex;
            justify-content: space-between;
            border-bottom: 1px solid #E9E8FC;
            &:last-child {
                border: none;
            }
        }

        &-item {
            &:last-child {
                flex-direction: column;
                display: flex;
                text-align: right;
            }
        }

        &-title {
            font-weight: normal;
            font-size: 16px;
            line-height: 22px;
            color: #27273F;
        }

        &-subtitle {
            font-weight: normal;
            font-size: 16px;
            line-height: 22px;
            color: #7C8DB0;
        }
    }

    &__total {
        margin-top: 16px;
        &-item {
            text-align: right;
        }

        &-text {
            font-weight: 600;
            font-size: 16px;
            line-height: 22px;
            color: #27273F;
            &:last-child {
                margin-left: 40px;
            }
        }
    }

    &__btn {
        float: right;
        margin-top: 40px;
    }

    &__img {
        margin-top: 104px;
        width: 382px;
        height: 525px;
    }
}